.main {
	width: 400px;
	min-height: 100px;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	gap: 10px;
	.icon {
		height: 30px;
		width: 30px;
		img {
			height: 30px;
			width: 30px;
			margin-right: 5px;
		}
	}
	@media screen and (max-width: 775px){
		flex-wrap: wrap;
	}
	.title {
		width: 100px;
	}
	.description {
		height: 100px;
		@media screen and (max-width: 775px){
			width: 100%;
		}
	}
	h3 {
		margin: 0;
	}
}


